import React from 'react'
import { Link, NavLink } from 'react-router-dom'

const NAVLINK = [
    { content: " ", link: "/sub01" },
    { content: "", link: "/sub02" },
    { content: "/", link: "/sub03" },
    { content: "", link: "/sub04" },
    { content: "", link: "/board" },
]
const NavMenu = () => {
    return (
        <ul>
            {
                NAVLINK.map((it, idx) => {
                    return (
                        <li key={idx}><NavLink to={it.link}>{it.content}</NavLink></li>
                    )
                })
            }
        </ul>
    )
}

export default NavMenu